import '../style/Navbar.css'

const Navbar = ({ navigate }) => {
    return ( 
        <div className="nav">
            <div className="buttonWrapper" >
                <div className='navlink' onClick={() => navigate('/')}> </div>
                <div className='navlink' onClick={() => navigate('/homoscope')}></div>
                <div className='navlink' onClick={() => navigate('/gossip')}></div>
                <div className='navlink' onClick={() => navigate('/about')}></div>
                <div className='navlink' onClick={() => navigate('/register')}> </div>
                <div className='navlink' onClick={() => navigate('/singNshit')}>  </div>
                <div className='navlink' onClick={() => navigate('/teacup')}> </div>
                <div className='navlink' onClick={() => navigate('/weekly')}> </div>
                <div className='navlink' onClick={() => navigate('/forum')}> </div>
            </div>
        </div>
    );
}
 
export default Navbar;